<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ 23621</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина.
            На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{ name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{ address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{ phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                картой при получении
              </span>
            </li>
          </ul>
        </div>

        <CartProductList
          :total-price="totalPrice"
          :products="products"
          :cart-products-count="cartProductsCount"
        />
      </form>
    </section>
  </main>
</template>

<script>
import CartProductList from '@/components/CartProductList.vue';

export default {
  name: 'OrderInfoPage',
  components: { CartProductList },
  data() {
    return {
      address: null,
      cartProductsCount: 0,
      email: null,
      name: null,
      phone: null,
      products: [],
      totalPrice: 0,
    };
  },
  methods: {
    getOrderInfo() {
      if (this.orderInfo) {
        this.address = this.orderInfo.address;
        this.cartProductsCount = this.orderInfo.basket.items
          .reduce((count, productInfo) => count + +productInfo.quantity, 0);
        this.email = this.orderInfo.email;
        this.name = this.orderInfo.name;
        this.phone = this.orderInfo.phone;
        this.products = this.orderInfo.basket.items;
        this.totalPrice = this.orderInfo.totalPrice;
      }
    },
  },
  created() {
    if (this.orderInfo && this.orderInfo.id === this.$route.params.id) {
      this.getOrderInfo();
      return null;
    }
    return this.$store.dispatch('loadOrderInfo', this.$route.params.id)
      .then(() => this.getOrderInfo());
  },
  computed: {
    orderInfo() {
      return this.$store.state.orderInfo;
    },
  },
};
</script>

